<template>
	<div id="index">
		<div id="Login-Rigist" v-if="showLogin==1" >
			<transition name="move" mode="out-in">
				<div class="Login" v-show="isLog">
					<mu-appbar title="Login" class="login-title">
					<mu-icon-button slot="left"><i class="fa fa-address-book"></i></mu-icon-button>
						<!--<mu-icon-button slot="right">12</mu-icon-button>-->
					</mu-appbar>
					<div style="display: flex;flex-wrap: wrap;">
						<i class="fa fa-user-circle" id="login-icon"></i>
						<mu-text-field v-model="login.username" label="账号" labelFloat  style="width: 80%;"/>
						<i class="fa fa-key" id="login-icon"></i>
						<mu-text-field v-model="login.password" label="密码" type="password" labelFloat  style="width: 80%;"/>
						<mu-raised-button label="登录" class="demo-raised-button" @click.native="loginWebIM" primary id="login-btn" style="width: 50%;"/>
						<mu-raised-button label="注册" class="demo-raised-button" @click.native="isLog = !isLog"  id="login-btn" style="width: 50%;"/>
					</div>
				</div>
			</transition>
			<transition name="move" mode="out-in">
				<div class="Login" v-show="!isLog">
					<mu-appbar title="Regist" class="login-title">
					<mu-icon-button slot="left"><i class="fa fa-address-book-o"></i></mu-icon-button>
						<!--<mu-icon-button slot="right">12</mu-icon-button>-->
					</mu-appbar>
					<div id="" style="display: flex;flex-wrap: wrap;">
						<i class="fa fa-user-circle-o" id="login-icon"></i>
						<mu-text-field v-model="regist.username"  label="注册账号" labelFloat style="width: 80%;"/>
						<i class="fa fa-key" id="login-icon"></i>
						<mu-text-field v-model="regist.password"  label="密码" type="password" labelFloat style="width: 80%;"/>
						<i class="fa fa-key" id="login-icon"></i>
						<mu-text-field v-model="regist.password2"  label="确认密码" type="password" labelFloat style="width: 80%;"/>
						<mu-raised-button label="注册" @click.native="registWebIM" class="demo-raised-button"  primary id="login-btn" style="width: 50%;"/>
						<mu-raised-button label="返回登录" @click.native="isLog = !isLog" class="demo-raised-button" id="login-btn" style="width: 50%;"/>
					</div>
				</div>
			</transition>
		</div>
		<div id="chat-group" v-else-if="showLogin==2">
			<mu-list style="max-width: 600px;margin: 0 auto;text-align: left;overflow-y:scroll">
			    <mu-sub-header>群组</mu-sub-header>
			    <mu-list-item v-for="item in listGroups" :title="item.name" :key="item.id" @click="toChatGroup(item)" :afterText="'人数'+item.affiliations_count">
			    	<img src="../assets/lts.png" slot="leftAvatar" style="width: 40px;height: 40px;"/>
			      	<i class="fa fa-comments" slot="right" style="font-size: 24px;"/>
			    </mu-list-item>
			</mu-list>
		</div>
		<div id="chat-room" v-else>
			<mu-flat-button :label="Group.name" class="demo-flat-button" primary/>
			<!--<mu-raised-button @click="loginWebIM3">登录</mu-raised-button>-->
			<mu-list style="max-width: 600px;margin: 0 auto;text-align: left;overflow-y: scroll;position: absolute;transform: translateX(-50%);left: 50%;height: 70%;">
			    
			    <mu-list-item v-for="item in chatList" :title="item.isMine?'':item.msg" :afterText="item.isMine?item.msg:''" :key="item.id" style="margin-bottom: 15px;">
			    	<div slot="right" v-if="item.isMine" style="text-align: center;"><span class="msg">{{login.username}}</span></br><img src="../assets/hsq.png" slot="right" style="width: 40px;height: 40px;"/></div>
			    	<div slot="left" v-else style="text-align: center;"><span class="msg">{{item.username}}</span></br><img src="../assets/zhu.png" slot="right" style="width: 40px;height: 40px;"/></div>
			    </mu-list-item>
			</mu-list>
			<div style="position: absolute;bottom: 10px;display: flex;justify-content: center;width: 100%;">
				<input @keyup.enter="loginWebIM3" hintText="请输入" v-model="msgValue"/>
				<!--<mu-text-field hintText="请输入" v-model="msgValue"  @keyup.enter="loginWebIM3"/>-->
				<mu-raised-button @click="loginWebIM3" style="margin-left: 20px;">发送</mu-raised-button>
			</div>
			<mu-flat-button label="返回列表" class="demo-flat-button" secondary style="position: absolute;right: 5px;top: 65px;" @click="leaveRoom" />
		</div>
		<!--<mu-raised-button @click="registWebIM">1啊啊</mu-raised-button>
		<mu-raised-button @click="loginWebIM">登录</mu-raised-button>
		<mu-raised-button @click="loginWebIM2">登录2</mu-raised-button>
		<mu-raised-button @click="loginWebIM3">2发送1</mu-raised-button>-->
		
		
		
		 <mu-snackbar v-if="snackbar" :message="snackmsg" action="关闭" @actionClick="snackbar=false" @close="snackbar=false"/>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				login:{
					username:'123',
					password:'123456'
				},
				regist:{
					username:'',
					password:'',
					password2:''
				},
				showLogin:1,
				isLog:true,
				snackbar:false,snackmsg:'',
				listGroups:[],
				Group:'',msgValue:'',
				chatList:[{username:'我',msg:"你好呀",isMine:true},{username:'蓝天白云',msg:"你好",isMine:false}],
			}
		},
		activated(){
			
		},
		created() { //立即执行
			console.log()
			console.log(WebIM)
			this.configWebIM()
		},
		methods: {
			isLogin(){
				this.showLogin=false
			},
			configWebIM(){
				var that = this
				window.conn = new WebIM.connection({
				    isMultiLoginSessions: WebIM.config.isMultiLoginSessions,
				    https: typeof WebIM.config.https === 'boolean' ? WebIM.config.https : location.protocol === 'https:',
				    url: WebIM.config.xmppURL,
				    heartBeatWait: WebIM.config.heartBeatWait,
				    autoReconnectNumMax: WebIM.config.autoReconnectNumMax,
				    autoReconnectInterval: WebIM.config.autoReconnectInterval,
				    apiUrl: WebIM.config.apiURL,
				    isAutoLogin: true
				})
				conn.listen({
				    onOpened: function ( message ) {          //连接成功回调
				        // 如果isAutoLogin设置为false，那么必须手动设置上线，否则无法收消息
				        // 手动上线指的是调用conn.setPresence(); 如果conn初始化时已将isAutoLogin设置为true
				        // 则无需调用conn.setPresence();  
				        console.log(message)
				        _IndicatorClose()
				        that.showLogin = 2
				        that.getlistGroups()
				        _Toast("登录成功","middle") 
				    },  
				    onClosed: function ( message ) {
				    	console.log(message)
				    },         //连接关闭回调
				    onTextMessage: function ( message ) {
				    	console.log(message)
				    	that.chatList.push({username:message.from,msg:message.data,isMine:false})
				    },    //收到文本消息
				    onEmojiMessage: function ( message ) {},   //收到表情消息
				    onPictureMessage: function ( message ) {}, //收到图片消息
				    onCmdMessage: function ( message ) {},     //收到命令消息
				    onAudioMessage: function ( message ) {},   //收到音频消息
				    onLocationMessage: function ( message ) {},//收到位置消息
				    onFileMessage: function ( message ) {},    //收到文件消息
				    onVideoMessage: function (message) {},   //收到视频消息
				    onPresence: function ( message ) {
				    	console.log(message)
				    	if(message.type=="memberJoinChatRoomSuccess"){
				    		_Toast(message.mid+"成功加入","top")
				    		_IndicatorClose()
				    	}else if(message.type=="leaveChatRoom"){
				    		_Toast(message.to+"成功退出","top")
				    		_IndicatorClose()
				    		that.getlistGroups()
				    	}
				    	
				    },       //处理“广播”或“发布-订阅”消息，如联系人订阅请求、处理群组、聊天室被踢解散等消息
				    onRoster: function ( message ) {},         //处理好友申请
				    onInviteMessage: function ( message ) {},  //处理群组邀请
				    onOnline: function () {},                  //本机网络连接成功
				    onOffline: function () {},                 //本机网络掉线
				    onError: function ( message ) {},          //失败回调
				    onBlacklistUpdate: function (list) {       //黑名单变动
				        // 查询黑名单，将好友拉黑，将好友从黑名单移除都会回调这个函数，list则是黑名单现有的所有好友信息
				        console.log(list);
				    },
				    onReceivedMessage: function(message){},    //收到消息送达服务器回执
				    onDeliveredMessage: function(message){},   //收到消息送达客户端回执
				    onReadMessage: function(message){},        //收到消息已读回执
				    onCreateGroup: function(message){},        //创建群组成功回执（需调用createGroupNew）
				    onMutedMessage: function(message){}        //如果用户在A群组被禁言，在A群发消息会走这个回调并且消息不会传递给群其它成员
				})
			},
			loginWebIM(){
				_Indicator()
				var options = {
				  apiUrl: WebIM.config.apiURL,
				  user: this.login.username,
				  pwd: this.login.password,
				  appKey: WebIM.config.appkey
				};
				conn.open(options);
				setTimeout(()=>{
					conn.setPresence();
					
				},2000)
			},
			registWebIM(){
				var that = this
				if(!this.regist.username){
					this.snackbar = true
					this.snackmsg = "账号不能为空"
					setTimeout(() => { this.snackbar = false }, 2000)
					return false
				}
				if(!this.regist.password){
					this.snackbar = true
					this.snackmsg = "密码不能为空"
					setTimeout(() => { this.snackbar = false }, 2000)
					return false
				}
				if(this.regist.password!=this.regist.password2){
					this.snackbar = true
					this.snackmsg = "两次输入密码不一致"
					setTimeout(() => { this.snackbar = false }, 2000)
					return false
				}
				var options = { 
				    username: this.regist.username,
				    password: this.regist.password,
				    nickname: this.regist.username,
				    appKey: WebIM.config.appkey,
				    success: function () {
				    	that.snackbar = true
						that.snackmsg = "注册成功!"
						setTimeout(() => { that.snackbar = false }, 2000)
				    	console.log("注册成功!")
				    },
				    error: function () {
				    	that.snackbar = true
						that.snackmsg = "换个账号试试~"
						setTimeout(() => { that.snackbar = false }, 2000)
				    	console.log("换个账号试试~")
				    },
				    apiUrl: WebIM.config.apiURL
				  }; 
				  conn.registerUser(options);
			},
			getlistGroups(){
				var limit = 20,
			    cursor = null,
			    that = this;
			    var options = {
			        limit: limit,
			        cursor: cursor,
			        success: function (resp) {
			            console.log("Response: ", resp);
			            that.listGroups = resp.data
			        },
			        error: function (e) {}
			    };
			conn.getChatRooms(options);
			},
			toChatGroup(item){
				_Indicator()
				console.log(item)
				this.Group = item
				 // 加入聊天室
			    conn.joinChatRoom({
			        roomId: item.id, // 聊天室id
			    });	
				this.showLogin = 3
//				this.getGroupMember(item)
			},
			getGroupMember(item){
				var pageNum = 1,
				    pageSize = 1000;
				    console.log(item)
				    var options = {
				        pageNum: pageNum,
				        pageSize: pageSize,
				        groupId: item.groupid,
				        success: function (resp) { console.log("Response: ", resp)},
				        error: function(e){}
				    };
				conn.listGroupMember(options);
			},
			leaveRoom(){
				// 退出聊天室
				_Indicator()
				this.showLogin = 2
			    conn.quitChatRoom({
			        roomId: this.Group.id // 聊天室id
			    });
			    
			},
			loginWebIM2(){
				var options = { 
				  apiUrl: WebIM.config.apiURL,
				  user: '007',
				  pwd: '123456',
				  appKey: WebIM.config.appkey
				};
				conn.open(options);
				setTimeout(()=>{
					conn.setPresence();
				},2000)
			},
			loginWebIM3(){
			    this.chatList.push({username:this.login.username,msg:this.msgValue,isMine:true})
				var id = conn.getUniqueId();         // 生成本地消息id
			    var msg = new WebIM.message('txt', id); // 创建文本消息
			    var option = {
			        msg: this.msgValue,          // 消息内容
			        to: this.Group.id,               // 接收消息对象(聊天室id)
			        roomType: true,
			        chatType: 'chatRoom',
			        success: function () {
			            console.log('send room text success');
			        },
			        fail: function () {
			            console.log('failed');
			        }
			    };
			    msg.set(option);
			    msg.setGroup('groupchat');
			    conn.send(msg.body);
				this.msgValue = ''

			}
		},
		mounted() { //挂载完成执行

		},

	}
</script>

<style>
	.Login{
		position: absolute;
		top: 30%;
		left: 50%;
		transform: translateX(-50%);
		transform: (-50%,-50%);
		max-width: 350px;
	}
	.login-title{
		text-align: left;font-size: 19px;height: 46px;
	}
	#login-icon{
		padding: 10px;
		font-size: 1rem;
		width: 15%;line-height: 60px;
		color: #666;
	}
	#login-btn{
		width: 100%;
		margin-top: 20px;
	}
	.move-enter-active,.move-leave-active{	
        transition: opacity .5s;
	}
	.move-enter{
		transition: opacity .5s;
	}
      .move-leave-to{
        opacity: 0;
        transition: all .8s ease-out;
        transform: translateX(-500px);
      }
      #chat-room{
      	width: 100%;
      	
      }
   ::-webkit-scrollbar{
	  display:none;
	}
	.msg{
		font-size: 11px;white-space: nowrap;text-overflow:ellipsis;overflow:hidden;
	}
</style>


